<template>
    <div class="container">
        <Test class="car" />

        <div class="color">
            <input type="color" v-model="color" />
        </div>
    </div>
</template>

<script>
import Test from '../../public/assets/Test.svg';

export default {
    data(){
        return {
            color: '#000',
        }
    },
    components: {
        Test
    },
    mounted(){
        for(let path of document.querySelectorAll('path')){
            const previousColor = path.style.fill;

            const mouseEnter = () => path.style.fill = 'red';
            const mouseLeave = () => path.style.fill = previousColor;
            const click = () => {
                path.style.fill = this.color;
                previousColor = this.color;
            };

            path.addEventListener("mouseenter", mouseEnter);

            path.addEventListener("mouseleave", mouseLeave);

            path.addEventListener("click", click);
        }
    }
};
</script>

<style lang="scss" scoped>
.container{
    display: flex;
}
</style>
